import { FunctionComponent, useMemo, type CSSProperties } from "react";
import styles from "./CardWithPrice.module.css";

type CardWithPriceType = {
  dimensionCode?: string;
  locationName?: string;
  price?: string;

  /** Style props */
  propWidth?: CSSProperties["width"];
  propWidth1?: CSSProperties["width"];
  propLeft?: CSSProperties["left"];
};

const CardWithPrice: FunctionComponent<CardWithPriceType> = ({
  dimensionCode,
  locationName,
  price,
  propWidth,
  propWidth1,
  propLeft,
}) => {
  const groupDivStyle: CSSProperties = useMemo(() => {
    return {
      width: propWidth,
    };
  }, [propWidth]);

  const divStyle: CSSProperties = useMemo(() => {
    return {
      width: propWidth1,
    };
  }, [propWidth1]);

  const fromStyle: CSSProperties = useMemo(() => {
    return {
      left: propLeft,
    };
  }, [propLeft]);

  return (
    <button className={styles.card1}>
      <div className={styles.unsplashnnzkznywhauParent}>
        <img
          className={styles.unsplashnnzkznywhauIcon}
          alt=""
          src={dimensionCode}
        />
        <div className={styles.destinationDetails}>
          <b className={styles.paris}>{locationName}</b>
          <div className={styles.parent} style={groupDivStyle}>
            <div className={styles.div} style={divStyle}>
              {price}
            </div>
            <div className={styles.from} style={fromStyle}>
              from
            </div>
          </div>
        </div>
      </div>
    </button>
  );
};

export default CardWithPrice;
